import React, {Component} from 'react';
import Typist from 'react-typist';
//import logo from './logo.svg';
import './components/Styles/App.css';
import About from './components/About';
import Navigation from './components/Navigation'
import Resume from './components/Resume'
import Contact from './components/Contact'
import Projects from './components/Projects'
import HomePage from './components/HomePage'
import {BrowserRouter, Route, Switch} from 'react-router-dom';


class App extends Component {
  constructor(){
    super()
    //all states involve showing different parts of the website so they all 
    //dont render at once
    this.state= {
      helloState: true,
      welcomeState: false,
      baseAppState: false,
      questionState: false
    }
    //bind equations so they can set state
    this.helloToWelcome = this.helloToWelcome.bind(this)
    this.welcomeToQuestion = this.welcomeToQuestion.bind(this)
    this.questionToBase = this.questionToBase.bind(this)
  }
  //-------------------------------------------------------------------------------
  //once page is loaded then it instantly goes into hello message
  componentDidMount(){
    this.helloTimerID = setTimeout(() =>
      //seamlessly goes into welocme message after 3.5 seconds
      this.helloToWelcome()
      ,3500)
  }
  //-------------------------------------------------------------------------------
  //called in componentDidMount() changes state so that welcome message shows
  //then sets another 3.5second timer to change to the question state
  helloToWelcome(){
    clearTimeout(this.helloTimerID)
    this.setState({
      helloState:false,
      welcomeState:true
    })
    this.welcomeTimerID = setTimeout(() =>
      this.welcomeToQuestion()
      ,3500
    )
  }
  //-------------------------------------------------------------------------------
  //Once called changes the boolean to show the question instead of welcome text
  welcomeToQuestion(){
    clearTimeout(this.welcomeTimerID)
    this.setState({
      welcomeState:false,
      questionState:true
    })
  }

  //-------------------------------------------------------------------------------
  //if yes is selected then go into the homepage
  questionToBase(){
    this.setState({
      questionState:false,
      baseAppState:true
    })
  }

  //-------------------------------------------------------------------------------
  //else put up an alert
  noSelected(){
    alert("It appears you have made a mistake, please rethink your answer.")
  }

  //-------------------------------------------------------------------------------

  //the render function is up now
  render(){
    return (
      <body>
        <div>
        </div>
        {/* if helloState then put hello message, else null*/}
        {this.state.helloState ? 
          <Typist cursor= {{blink:true,show:true,element:'_',hideWhenDone:false,hideWhenDoneDelay:1000}}>
            <p className="hello">Hello.</p>
          </Typist>
        :
        null}
        {/* if welcomeState then put welcome message, else null*/}
        {this.state.welcomeState ? 
          <Typist cursor= {{blink:true,show:true,element:'_',hideWhenDone:false,hideWhenDoneDelay:1000}}>
            <p className="hello">Welcome to my Website!</p>
          </Typist>  
        :
        null}
        {/* if questionState then put question message, else null*/}
        {this.state.questionState ? 
        <Typist>
          <p className="hello">
            Would you like to Continue?
            <br></br>
            <button onClick={this.questionToBase}>YES</button>
            <button onClick={this.noSelected}>NO</button>
          </p>
        </Typist>: null}
        {/* Router to be able to change between different pages seamlessly*/}
         {this.state.baseAppState ? 
          <BrowserRouter basename={process.env.PUBLIC_URL + "/"}>
            <div>
              <Navigation />
              <Switch>
                <Route path = "/" component={HomePage} exact/>
                <Route path ="/about" component={About}/>
                <Route path="/resume" component={Resume}/>
                <Route path="/projects" component={Projects}/>
                <Route path="/contact" component={Contact}/>
              </Switch>
            </div>
          </BrowserRouter>
          : null}
      </body>
    );
  }
}

export default App;
